@keyframes headerScrollIn {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes headerScrollOut {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-100%);
  }
}

.section {
  &__select {
    transition: all 650ms ease-in-out;
  }
  &__hidden {
    opacity: 0;
    transform: translateY(8rem);
    transition: all 650ms ease-in-out;
  }
}
