@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap");
:root {
  --red: #b92222;
  --white: #fff;
  --black: #0f172a;
}

:root {
  --font-Sans: "Open Sans", sans-serif;
  --font-Poppins: "Poppins", sans-serif;
  --font-Normal: 400;
  --font-semiBold: 600;
  --font-Bold: 700;
}

@keyframes headerScrollIn {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes headerScrollOut {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-100%);
  }
}
.section__select {
  transition: all 650ms ease-in-out;
}
.section__hidden {
  opacity: 0;
  transform: translateY(8rem);
  transition: all 650ms ease-in-out;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

body {
  position: relative;
  font-family: var(--font-Poppins);
  font-size: 16px;
  overflow-x: hidden;
}
@media screen and (max-width: 768px) {
  body.no-scroll {
    overflow: hidden;
  }
}

img {
  height: auto;
  max-width: 100%;
}

ul,
ol {
  list-style: none;
}

button,
input {
  font-family: var(--fontNormal);
}

a,
a:visited,
a:active {
  text-decoration: none;
}
a:focus,
a:visited:focus,
a:active:focus {
  outline: none;
}
a:focus-visible,
a:visited:focus-visible,
a:active:focus-visible {
  outline: 2px solid var(--red);
  outline-offset: 3px;
}

.container {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease-in-out;
}
@media screen and (max-width: 768px) {
  .overlay.overlay-active {
    visibility: visible;
    opacity: 1;
  }
}

.skip-to-content-link {
  position: fixed;
  top: 110px;
  left: 0;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
  padding: 20px 40px;
  background-color: var(--white);
  font-size: 15px;
  text-decoration: underline;
  z-index: 100;
  transform: translateX(-200px);
  transition: transform 300ms ease;
  color: var(--black);
}
.skip-to-content-link:focus {
  outline: 3px dashed var(--red);
  outline-offset: 0;
  transform: translateX(0);
}

h1,
h2,
h3,
h4,
p {
  font-family: var(--font-Sans);
}

.heading, .heading--xxl {
  color: var(--black);
  font-weight: 400;
}
.heading__name {
  color: var(--red);
}
.heading--intro {
  color: var(--black);
  font-size: clamp(30px, 5vw, 42px);
  text-align: center;
  font-weight: 600;
}
.heading--xxl {
  font-size: clamp(34px, 6vw, 53px);
  max-width: 430px;
  font-weight: 700;
  margin-bottom: 20px;
}
.heading--xl {
  position: relative;
  font-size: clamp(17px, 4vw, 23px);
  font-weight: 700;
  text-align: center;
  color: var(--red);
  margin-bottom: 29px;
  text-transform: uppercase;
}
.heading--xl::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -5px;
  height: 5px;
  width: 64px;
  margin: 0 auto;
  background-color: var(--red);
}
.heading--l {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  color: var(--black);
  margin-bottom: 19px;
}
.heading--m {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 14px;
  color: var(--white);
}
.heading--services {
  text-transform: uppercase;
  font-weight: 600;
  text-align: center;
  margin: 15px 0;
}
.heading--footer {
  font-size: 40px;
}

.paragraph, .paragraph--l {
  line-height: 1.2;
}
.paragraph__name {
  color: var(--black);
  font-weight: 700;
}
.paragraph--l {
  font-size: clamp(23px, 3vw, 27px);
  margin-bottom: 26px;
}
.paragraph--about {
  font-size: 16px;
  margin-bottom: 17px;
  line-height: 28px;
}

.learnMore__text {
  color: var(--red);
  font-weight: 700;
  font-size: 17px;
  letter-spacing: 1px;
  margin-right: 18px;
  transition: margin-right 0.3s ease-in-out;
}
.learnMore__icon svg path {
  fill: var(--red);
}

.btn, .btn--start, .btn--blue, .btn--menu {
  position: relative;
  outline: none;
  border: none;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 300ms, border 300ms ease-in;
}
.btn:focus, .btn--start:focus, .btn--blue:focus, .btn--menu:focus {
  outline: none;
}
.btn:focus-visible, .btn--start:focus-visible, .btn--blue:focus-visible, .btn--menu:focus-visible {
  outline: 3px solid var(--red);
  outline-offset: 3px;
}
.btn--menu {
  display: flex;
  justify-self: center;
  align-items: center;
  background-color: transparent;
  z-index: 10;
}
.btn--menu:focus {
  outline: 3px solid var(--red);
  outline-offset: 3px;
}
@media screen and (min-width: 769px) {
  .btn--menu {
    display: none;
  }
}
.btn--blue {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--red);
  border: 2px solid var(--red);
  border-radius: 4px;
  color: var(--white);
  box-shadow: 0px 10px 15px -7px var(--black);
}
.btn--blue:hover {
  background-color: var(--white);
  color: var(--red);
}
.btn--start {
  width: 180px;
  height: 48px;
}
.btn--more:hover .learnMore__text {
  margin-right: 26px;
}
.btn--view {
  font-size: 16px;
  color: var(--black);
}

.icon__skills {
  color: var(--red);
  font-size: clamp(22px, 4vw, 29px);
}

.icon__project {
  margin-right: 10px;
}

.header {
  position: absolute;
  left: 0;
  width: 100%;
  padding: 24px 28px 0;
  z-index: 999;
}
.header.header--in {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 12px 28px;
  transition: all 0.3s ease-in-out;
  background-color: var(--white);
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.04);
  animation: headerScrollIn 700ms ease-in-out forwards;
}
.header.scroll-out {
  animation: headerScrollOut 250ms ease-in-out forwards;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 51px;
  transition: all 350ms ease-in-out;
}
.header__logo {
  z-index: 1;
  font-size: 24px;
  color: var(--black);
}
@media screen and (max-width: 768px) {
  .header__logo.active-color {
    color: var(--white);
  }
}
.header__logo--letter {
  font-size: 30px;
  color: var(--red);
}
@media screen and (max-width: 768px) {
  .header__menu {
    position: fixed;
    width: 100%;
    top: 0px;
    right: -1000px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--black);
    transition: all 500ms ease-in;
  }
  .header__menu.is-active {
    right: 0px;
  }
}
.header__list {
  display: flex;
  align-items: center;
}
@media screen and (min-width: 769px) {
  .header__item:not(:last-child) {
    margin-right: 28px;
  }
}
@media screen and (max-width: 768px) {
  .header__list {
    display: grid;
    grid-template-columns: max-content;
    grid-template-rows: repeat(5, 3.75rem);
    align-items: center;
    justify-items: center;
  }
}
.header__link {
  position: relative;
  font-size: 17px;
  font-weight: 600;
  color: var(--white);
  text-transform: uppercase;
}
.header__link:active {
  outline: 3px solid var(--white);
  outline-offset: 3px;
}
.header__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  height: 4px;
  width: 100%;
  background-color: var(--red);
  visibility: hidden;
  opacity: 0;
  transform-origin: left;
  transform: scaleX(0);
}
@media screen and (min-width: 769px) {
  .header__link {
    color: var(--black);
    font-size: 15px;
  }
  .header__link:hover {
    color: var(--red);
  }
  .header__link:hover::before {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
    transition: all 350ms ease-in;
  }
}

.hero {
  position: relative;
  padding: 166px 28px 80px;
}
.hero__container {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 769px) {
  .hero__container {
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
}
.hero__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
@media screen and (min-width: 769px) {
  .hero__text {
    align-items: flex-start;
    flex: 1 0 50%;
    text-align: left;
  }
}

.about {
  padding: 80px 28px 20px;
}
.about__container {
  display: flex;
  flex-direction: column;
  grid-gap: 29px;
  justify-items: center;
  align-items: center;
}
@media screen and (min-width: 769px) {
  .about__container {
    display: grid;
    grid-template-columns: 1fr 1.4fr;
    grid-template-rows: 290px 1fr;
  }
}
.about__img {
  width: 286px;
  height: 270px;
  object-fit: cover;
}
.about__hidden {
  opacity: 0;
  transform: translateY(9rem);
  transition: all 350ms ease-in-out;
}

.services {
  padding: 20px 19px 140px;
}
.services__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 294px;
  justify-items: center;
  align-items: center;
  grid-gap: 22px;
}
@media screen and (max-width: 768px) {
  .services__container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 576px) {
  .services__container {
    grid-template-columns: 1fr;
  }
}
.services__images {
  width: 205px;
  height: 116px;
}
.services__service {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.skills {
  position: relative;
  padding: 0 18px;
}
.skills__container {
  display: flex;
  flex-direction: row;
  gap: 20px;
}
.skills__tech, .skills__tools {
  display: flex;
  flex-direction: column;
  justify-self: center;
  width: 100%;
}
.skills__names {
  display: grid;
  grid-template-columns: 50px 1fr;
  align-items: center;
  width: 100%;
  font-size: clamp(12px, 3vw, 15px);
  font-weight: 500;
  text-align: left;
  padding: 10px 14px;
  margin-bottom: 15px;
  box-shadow: 0 0 6px 2px rgba(48, 48, 48, 0.1);
}

.projects {
  padding: 170px 28px;
}
.projects__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  grid-gap: 40px;
  margin-top: 30px;
}
@media screen and (max-width: 768px) {
  .projects__container {
    grid-template-columns: 1fr;
  }
}

.project {
  position: relative;
  overflow: hidden;
}
.project__img {
  display: block;
  min-height: 100%;
  width: 100%;
  border-radius: 8px;
}
.project__overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: opacity 0.35s;
  background: rgba(0, 0, 0, 0.815);
  color: var(--white);
  display: flex;
  flex-direction: column;
  opacity: 0;
}
.project__overlay:hover {
  opacity: 1;
  cursor: pointer;
}
.project__overlay:hover > * {
  transform: translateX(0px);
  transition: transform 0.35s;
}
.project__overlay > * {
  transform: translateX(-20px);
  transition: transform 0.35s;
}
.project__overlay__desc {
  max-width: 100%;
  margin: auto 0;
  padding: 0.5rem 3rem;
}
@media screen and (max-width: 768px) {
  .project__overlay__desc {
    padding: 0.5rem 1rem;
  }
}
.project__description {
  font-size: clamp(13px, 3vw, 16px);
}
.project__view, .project__code {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 38px;
  font-size: 14px;
  transition: all 0.3s ease;
  border-radius: 7px;
}
@media screen and (max-width: 768px) {
  .project__view, .project__code {
    font-size: 11px;
    height: 28px;
  }
}
.project__code {
  background-color: #343a40;
  border-color: #343a40;
  color: var(--white);
  width: 180px;
  transition: all 0.3s ease;
}
@media screen and (max-width: 768px) {
  .project__code {
    width: 135px;
  }
}
.project__code:hover {
  background-color: #343a40c9;
}
.project__view {
  background-color: #868d94;
  border-color: #868d94;
  color: var(--white);
  transition: all 0.3s ease;
  width: 130px;
}
@media screen and (max-width: 768px) {
  .project__view {
    width: 90px;
  }
}
.project__view:hover {
  background-color: #868d94ce;
}
.project__link {
  display: flex;
  gap: 14px;
  margin-top: 16px;
}
.project__title {
  font-size: clamp(20px, 4vw, 30px);
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 6px;
}

.footer {
  position: relative;
  background-color: var(--black);
  padding: 120px 50px 80px;
}
.footer__container {
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  grid-template-areas: "location email number" "copy copy links";
  grid-template-rows: 140px 1fr;
  justify-items: center;
  margin-top: 50px;
  color: #94a9db7e;
}
.footer__container h5 {
  color: var(--white);
  text-align: center;
}
@media screen and (max-width: 768px) {
  .footer__container {
    grid-template-columns: 1fr;
    grid-template-areas: "location" "email" "number" "copy" "links";
    grid-template-rows: auto;
    grid-gap: 24px;
  }
}
.footer__location {
  grid-area: location;
}
.footer__email {
  grid-area: email;
}
.footer__number {
  grid-area: number;
}
.footer__copyright {
  grid-area: copy;
  font-size: 14px;
}
.footer__links {
  grid-area: links;
}
.footer__links__link {
  padding: 0 10px;
}
.footer__links__link:hover .footer__links__icon {
  color: var(--red);
  transition: color 350ms ease-in-out;
}
.footer__links__icon {
  color: var(--white);
  font-size: 22px;
  transition: color 350ms ease-in-out;
}